            // " 1989 .  .     ,            . ,          .          ,    .          ,   2016-      .\n     .         -:  ,   ,        ,             80-.\n          ( )      .    ,            ,      .\n ,    ,       ,      .              :             .",
          "The play was created with the support of the Government of Moscow and the Department of Culture of Moscow. It is a member of the state and public support program for theatres for children and adolescents under the patronage of the President of the Russian Federation.\nFebruary, 1989. Tomsk Region. In a small village there was an event, which mankind had dreamed of for hundreds of years: the alien met the earthly one, though it looked little like the plots of sci-fi films and books. The locals cared more about the food stamps and survival rather than the contact with the aliens. So nobody knew about this event of the universal significance until the group of theatre artists took interest in it in 2016.\nThe play is in the form of a museum tour. The spectators study the artifacts and evidence of events, accompanied by the actors / tour guides: drawings of scientists, interviews with eyewitnesses, reconstruction of spacesuits and other traces of the presence of extraterrestrial life; all while making a historical trip into recent past  into the disintegrating USSR of the late 80s.\nAlien Invasion Museum is made in an uncommon to the Russian viewer genre of mockumentary (fake documentary) and is designed for the widest audience. Children will be interested in a sci-fi story, and adults will be able to see a parallel story: one of xenophobia and tolerance, the social paradoxes of our yesterday and today. Moreover, we are presented with the artists theatre, three famous artists united in the group Theatre of Mutual Operations, more precisely. One of them, Alexey Lobanov, is close and dear for The Access Point: he was one of the co-authors of our very first play Towards White KAMAZ."
