          "This is the first Russian direction of Bertolt Brecht's play written in 1940-41 in Finland. The story is set at Helsinki Central Station, and the actions take place in the halls, cafes and on the station platforms. With the help of surveillance equipment, the audience will have an opportunity to follow up the private conversation of Ziffel And Kalle. Every spectator can choose the way of surveillance for themselves: you can follow the artists or dissolve in the crowd and create your own visual imagery that spontaneously matches with sounds and noises around. The main characters of the play, whose voices accompany the viewer throughout the performance, are two German refugees from World War II times that discuss both global human issues and daily hardships. The Kalles image discern the features of Brecht himself. Perhaps the play will still seem relevant for the modern audience."
